.productStyle {
    display: flex;
    gap: 30px;
    align-items: center;
}

.input__wrapper {
    user-select: none;
    padding-top: 22px;
    input {
        border: none;
        outline: none;
    }
}

.productStyleCheck {
    width: 240px;
    display: flex;
    margin-top: 35px;
    align-items: center;
}

input[type='checkbox'] {
    width: 20px;
    height: 20px;
}

.addProductBtn {
    width: 384px;
    height: 64px;
    background-color: #19D476;
    border: none;
    border-radius: 10px;
    margin-bottom: 112px;
}

.linkText {
    color: #4E72CA;
    border-bottom:1px solid #4E72CA;
    margin-left: 67px;
    margin-bottom: 112px;
}

.bradius {
    border: none;
    outline: none;
    border-bottom: 1.5px solid rgba(0, 0, 0, 0.4);
    padding: 0;
    margin: 0;
    padding-bottom: 7px;
}

.file {
    display: none;
}

.fileWrapper {
    gap: 38px;
    width: 688px;
    display: flex;
    flex-wrap: wrap;
    align-items: flex-end;
    justify-content: space-between;
}

.productWrapper {
    padding-left: 238px;
    user-select: none;
}

.prContact {
    display: flex;
    flex-direction: column;
    gap: 31px;
    margin-bottom: 45px;
}

.prContactText {
    margin: 0;
    margin-bottom: 15px;
}

.prContactInp {
    width: 310px;
}

.raySelect {
    width: 338px;
    margin-left: 30px;
}

.regSelect {
    width: 229px;
}

.imputTitle {
    font-family: 'Gilroy';
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 17px;
    color: #000000;
    margin-top: 39px;
}

.checkTitle {
    font-family: 'Gilroy';
    font-style: normal;
    font-weight: 500;
    font-size: 15px;
    line-height: 18px;
    color: #000000;
    padding-left: 11px;
}

.mainTitle {
    font-family: 'Gilroy';
    font-style: normal;
    font-weight: 500;
    font-size: 19px;
    line-height: 23px;
}