html {
  height: auto;
}

body {
  font-family: 'Gilroy';
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #FFFFFF;
  color: black;
}
#root {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  --white: #fff !important;
  background-color: #fff;
}

ul, li, a, p, pre, body {
  margin: 0;
  padding: 0;
  text-decoration: none;
  list-style-type: none;
}

code {
  font-family: 'Gilroy';
}
