.shop {
    padding-top: 80px;
    li {
        justify-content: space-between;
        gap: 25px;
    }
    &__short , &__item, &__hi{
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
    }
    &__item {
        width: 265px;
    }
    
    &__img {
        width: 200px;
        height: 200px;
        object-fit: contain;
    }
    
    &__header {
        font-family: 'Gilroy';
        font-style: normal;
        font-weight: 500;
        font-size: 16px;
        line-height: 19px;
        color: #000000;
    }
    
    &__price {
        font-family: 'Gilroy';
        font-style: normal;
        font-weight: 700;
        font-size: 16px;
        line-height: 20px;
        color: #000000;
    }
    
    &__btn-wrapper {
        display: flex;
        align-items: center;
        margin-left: auto;
        margin-right: auto;
        justify-content: center;

    }
    
    &__more-btn {
        width: 99px;
        height: 18px;
        font-size: 15px;
        line-height: 18px;
        color: #4E72CA;
        border: none;
        outline: none;
        ::marker {
            color: #4E72CA;
        }
        summary {
        color: black !important;

        }

    }
    
    &__time {
        font-family: 'Gilroy';
        font-style: normal;
        font-weight: 400;
        font-size: 12px;
        line-height: 14px;
        color: #4A4A4A;
    }
    
}