.profile {
   padding-top: 16px;
   padding-bottom: 17px;
}

.profile__top-wrapper {
   div {
      justify-content: space-between;
      div {
         .one {
            width: 198px;
            border: 1px solid rgba(151, 151, 151, 0.496575);
            border-top-left-radius: 22.5px;
            border-bottom-left-radius: 22.5px;
            border-right: none;
         }
         .three {
            position: relative;
            border-left: none;
            border: 1px solid rgba(151, 151, 151, 0.496575);
            border-top-right-radius: 22.5px;
            border-bottom-right-radius: 22.5px;
            
            ::before {
               content: '';
               position: absolute;
               left: 0;
               right: 0;
               background-color: #ff0;
               display: inline-block;
               border-bottom: 1px solid rgba(151, 151, 151, 0.370273) ;
               width: 20px;
               height: 1px;
            }
         }
      }
   }
}

.profile__select {
   font-family: 'Gilroy';
   font-style: normal;
   font-weight: 500;
   font-size: 15px;
   line-height: 18px;
   padding-top: 13px;
   padding-bottom: 14px;
   color: #000000;
}

.profile__input {
   font-family: 'Gilroy';
   font-style: normal;
   font-weight: 500;
   font-size: 15px;
   line-height: 18px;
   padding-top: 13.5px;
   padding-bottom: 15px;
   color: #000000;
   border-right: none;
   border-left: none;
   border: 1px solid rgba(151, 151, 151, 0.496575);
   
}


.profile__btn {
   font-family: 'Gilroy';
   font-style: normal;
   font-weight: 700;
   font-size: 14px;
   line-height: 17px;
   color: #FFFFFF;
   background: #19D476;
   border-radius: 22px;
   outline: none;
   border: none;
   padding: 15px 19px 16px 19px;
}


.profile__list {
   
}

.profile__item {
   .active {
      color: #304FFF;
   }
   a {
      color: #414D6A;
   }
}
























