.nested {
    border-bottom: 1px solid #D5D5D5;
    padding-top: 31px;
    user-select: none;
}

h2 {
    user-select: none;
}

.nested__wrapper {
    display: flex;
    gap: 77px;
    padding-bottom: 15px;
    div {
        transition: all 0.5s ease;
    } 
    
}

.nested__link {
    position: relative;
    transition: all 0.5s ease;
}

.nested__inner {
}

.nestedive {
    color: #4E72CA;
}


.top {
    .nestedive {
        font-weight: 900;
        color: #4E72CA;
        p {
            
            color: #4E72CA;
        }
        ::before {
            display: inline-block;
            position: absolute;
            content: '';
            width: 127px;
            height: 2px;
            bottom: -16.5px;
            left: 20%;
            background-color: #4E72CA;
            transition: all 0.5s ease;
        }
    }
}

.bottom {
    .nestedive {
        font-weight: 900;
        color: #4E72CA;
        padding-bottom: 15px;
        p {color: #4E72CA;}
        ::before {
            display: inline-block;
            position: absolute;
            content: '';
            width: 127px;
            height: 2px;
            bottom: -16.5px;
            left: 9%;
            background-color: #4E72CA;
        }
        
        
        
    }
}

.nested__text {
    font-family: 'Gilroy';
    font-style: normal;
    font-weight: 700;
    font-size: 24px;
    line-height: 30px;
    color: #9B9B9B;
    transition: all 0.5s ease;
    transform: translateX(10);
}


.margin__tb {
    margin-top: 40px;
    margin-bottom: 40px;
}