@import url('https://fonts.googleapis.com/css?family=Fira+Mono:400');

.error__body{ 
  display: flex;
  width: 100%;
  height: 50vh;
  justify-content: center;
  color: #fff;
  font-size: 96px;
  font-family: 'Fira Mono', monospace;
}

.error__header{
  animation: Toxirrrr 1.8s linear infinite;
}

@keyframes Toxirrrr{
  2%,64%{
    transform: translate(2px,0) skew(0deg);
  }
  4%,60%{
    transform: translate(-2px,0) skew(0deg);
  }
  62%{
    transform: translate(0,0) skew(5deg); 
  }
}

.error__body:before, .error__body:after {
  content: attr(title);
  position: absolute;
  left: 0;
  top: 20px;
}

.error__body:before {
  animation: ToxirrrrTop 1s linear infinite;
  clip-path: polygon(0 0, 100% 0, 100% 33%, 0 33%);
  -webkit-clip-path: polygon(0 0, 100% 0, 100% 33%, 0 33%);
}

@keyframes ToxirrrrTop{
  2%,64%{
    transform: translate(2px,-2px);
  }
  4%,60%{
    transform: translate(-2px,2px);
  }
  62%{
    transform: translate(13px,-1px) skew(-13deg); 
  }
}

.error__body:after {
  animation: ToxirrrrBotom 1.5s linear infinite;
  clip-path: polygon(0 67%, 100% 67%, 100% 100%, 0 100%);
  -webkit-clip-path: polygon(0 67%, 100% 67%, 100% 100%, 0 100%);
}

@keyframes ToxirrrrBotom {
  2%,64%{
    transform: translate(-2px,0);
  }
  4%,60%{
    transform: translate(-2px,0);
  }
  62%{
    transform: translate(-22px,5px) skew(21deg); 
  }
}