.login {
	padding-bottom: 200px;
}

body {
	align-items: center;
	background-color: var(--white);
	background-attachment: fixed;
	background-position: center;
	background-repeat: no-repeat;
	background-size: cover;
	height: 100vh;
	place-items: center;
}

.form__title {
	font-weight: 300;
	margin: 0;
	margin-bottom: 1.25rem;
}

.login__top-text {
	font-family: 'Gilroy';
	font-weight: 500;
	font-size: 17px;
	line-height: 23px;
	text-align: center;
	color: #304FFF;
	width: 334px;
}

.link {
	color: var(--gray);
	font-size: 0.9rem;
	margin: 1.5rem 0;
	text-decoration: none;
}

.container__login {
	background-color: var(--white);
	border-radius: var(--button-radius);
	box-shadow: 0 0.9rem 1.7rem rgba(0, 0, 0, 0.25),
	0 0.7rem 0.7rem rgba(0, 0, 0, 0.22);
	height: var(--max-height);
	max-width: var(--max-width);
	overflow: hidden;
	position: relative;
	width: 100%;
	margin-top: 100px;
}

.login__left {
	margin-top: 80px;
	overflow-y: hidden;
	align-items: center;
	text-align: center;
	p {
		font-family: 'Gilroy';
		font-style: normal;
		font-weight: 500;
		font-size: 17px;
		line-height: 23px;
		text-align: center;
		color: #304FFF;
		width: 334px;
		
	}
}

.login__header {
	color: #000;
	padding-bottom: 10px;
	margin: 0;
	margin-left: auto;
	margin-right: auto;
}

.login__bottom {
	display: flex;
}

.btn {
	cursor: pointer;
	font-size: 0.8rem;
	font-weight: bold;
	letter-spacing: 0.1rem;
	padding: 0.9rem 4rem;
	text-transform: uppercase;
	transition: transform 80ms ease-in;
	margin-top: 31px;
	background: #41D481;
	border: none;
	border-radius: 5px;
	width: 329px;
	font-weight: 500;
	font-size: 17px;
	line-height: 21px;
	text-align: center;
	color: #FFFFFF;
}

.btn:active {
	transform: scale(0.95);
}

.btn:hover {
	transform: scale(1.01);
}

.btn:focus {
	outline: none;
}

.login__top-text {
	
}

.form {
	background-color: var(--white);
	display: flex;
	flex-direction: column;
	height: 100%;
	width: 350px;
	border-radius: 10px;
	transition: all 0.4s ease;
	color: #e9e9e9;
	overflow: hidden;
	align-items: center;
}

.login__input {
	padding: 0.9rem 0.9rem;
	padding: 20px 0 19px 24.5px;
	width: 90%;
	background: #FFFFFF;
	border: 1px solid rgba(151, 151, 151, 0.482592);
	border-radius: 5px;
	width: 313px;
}


.login__label {
	font-weight: 500;
	font-size: 17px;
	line-height: 21px;
	color: #787993;
	padding-left: 0;
	margin-bottom: 7px;
	margin-top: 24px;
	margin-right: auto;
	margin-left: 5px;
}

.display-flex {
	display: flex;
}

.display-none {
	display: none;
}

.footer__span-wrapper {
	display: flex;
	gap: 13px;
	margin-top: 49px;
}

.login__span {
	height: 2px;
	border-top: 1px solid #979797;
	display: flex;
	width: 100px;
}

.login__text {
	font-family: 'Gilroy';
	font-weight: 500;
	font-size: 14px;
	line-height: 17px;
	text-align: center;
	color: #787993;
}

.login__recovery {
	margin-top: 16px;
}
@keyframes show {
	0%,
	49.99% {
		opacity: 0;
		z-index: 1;
	}
	
	50%,
	100% {
		opacity: 1;
		z-index: 5;
	}
}

.login__right {
	margin-left: 92px;
	width: 654px;
	background-image: url(./../../assets/img/login__bgi.jpg);
	padding: 372px 0 185px 61px;
	background-repeat: no-repeat;
}

.login__r-wrapper {
	width: 250px;
	border-radius: 220px 220px 220px 25px;
	background: #FFFFFF;
	padding: 80px 0 50px 51px;
	
}


.login__r-text {
	font-weight: 700;
	font-size: 19px;
	line-height: 21px;
	color: #787993;
	width: 170px;
	margin: 0;	
	margin-bottom: 18px;
	margin-top: 7px;
}

.login__btn {
	font-weight: 700;
	font-size: 16px;
	line-height: 20px;
	color: #41D481;
	border: 2px solid #41D481;
	border-radius: 5px;
	padding: 8px 17px 10px 18px;
	background: #FFFFFF;
	width: 167px;
	cursor: pointer;
	
	&:hover {
		transform: scale(1.05);
	}
	&:active {
		transform: scale(0.95);
	}
}