.product {

    &__top {
        background: #FFFFFF;
        box-shadow: 0px 8px 14px 0px rgb(0 0 0 / 6%);
        padding-top: 16px;
        padding-bottom: 17px;
        margin-bottom: 24px;
    }

    &__top {
        & .marmin {
            margin-bottom: 0px;
        }
    }

    &__top-wrapper {
        max-width: 1140px;
        margin-left: auto;
        margin-right: auto;
        display: flex;
    }

    &__top-left {
        display: flex;
        align-items: center;
        background: #FFFFFF;
        border: 1px solid rgba(151, 151, 151, 0.496575);
        border-radius: 22.5px;
        height: 45px;
        margin-right: 29px;
    }

    &__top-category {
        display: flex;
        align-items: center;
        gap: 12px;
        padding: 13px 18px 14px 30px;
        border-right: 1px solid rgba(151, 151, 151, 0.370273);
        font-family: 'Gilroy';
        font-style: normal;
        font-weight: 500;
        font-size: 15px;
        line-height: 18px;
        color: #000000;
    }

    &__top-arrow {}

    &__top-search {
        font-family: 'Gilroy';
        font-style: normal;
        font-weight: 500;
        font-size: 15px;
        line-height: 18px;
        color: #000000;
        padding: 13px 199px 14px 25px;
        border-right: 1px solid rgba(151, 151, 151, 0.370273);
    }

    &__top-location {
        display: flex;
        align-items: center;
        font-family: 'Gilroy';
        font-style: normal;
        font-weight: 500;
        font-size: 15px;
        line-height: 18px;
        color: #000000;
        gap: 28px;
        padding: 13px 18px 14px 24px;
    }

    &__top-btn {
        font-family: 'Gilroy';
        font-style: normal;
        font-weight: 700;
        font-size: 14px;
        line-height: 17px;
        color: #FFFFFF;
        padding: 14px 18px 13px 19px;
        background: #19D476;
        border: none;
        border-radius: 22px;
    }

    &__top-right {
        display: flex;
        align-items: center;
        font-family: 'Gilroy';
        font-style: normal;
        font-weight: 700;
        font-size: 15px;
        line-height: 19px;
        color: #FFFFFF;
        background: #19D476;
        border-radius: 22px;
        padding: 0 30px 0 0;
    }

    &__top-plus {
        font-family: 'Gilroy';
        font-size: 28px;
        line-height: 33px;
        color: #FFFFFF;
        padding: 6px 13px 4px 15px;
        background: #11B463;
        border-radius: 22px;
        margin-right: 12px;
        text-align: center;
        display: flex;
        align-items: center;
        justify-content: center;
        cursor: pointer;
    }

    &__middle {
        display: flex;
        flex-direction: column;
    }

    &__breadcumb {
        font-family: 'Gilroy';
        font-style: normal;
        font-weight: 500;
        font-size: 14px;
        line-height: 17px;
        color: #787993;
        margin-bottom: 20px;
    }

    &__heading {
        font-family: 'Gilroy';
        font-style: normal;
        font-weight: 500;
        font-size: 34px;
        line-height: 41px;
        color: #414D6A;
        margin-bottom: 14px;
    }

    &__middle-wrapper {
        display: flex;
        gap: 32px;
    }

    &__middle-images {
        display: flex;
        gap: 17px;
    }

    &__images-left {
        display: flex;
        flex-direction: column;
        gap: 8px;
        max-width: 113px;
        width: 100%;
    }

    &__left-img {
        width: 85px;
        height: 85px;
        object-fit: cover;
    }

    &__images-right {
        width: 456px;
        height: 456px;
    }

    &__right-img {
        width: 456px;
        height: 456px;
        object-fit: cover;
    }

    &__middle-text-wrapper {
        display: flex;
        flex-direction: column;
        max-width: 555px;
        width: 100%;
    }

    &__middle-articul-wrapper {
        display: flex;
        align-items: center;
        margin-bottom: 17px;
        justify-content: space-between;
    }

    &__middle-articul {
        font-family: 'Gilroy';
        font-style: normal;
        font-weight: 500;
        font-size: 14px;
        line-height: 17px;
        letter-spacing: -5.89155e-05px;
        color: #414D6A;
    }

    &__middle-time {
        font-family: 'Gilroy';
        font-style: normal;
        font-weight: 500;
        font-size: 14px;
        line-height: 17px;
        letter-spacing: -5.89155e-05px;
        color: #414D6A;
    }

    &__middle-price {
        font-family: 'Gilroy';
        font-style: normal;
        font-weight: 500;
        font-size: 40px;
        line-height: 49px;
        letter-spacing: -0.00016833px;
        color: #444444;
        margin-bottom: 8px;
    }

    &__middle-location {
        font-family: 'Gilroy';
        font-size: 14px;
        line-height: 16px;
        letter-spacing: -5.89154e-05px;
        text-decoration-line: underline;
        color: #4E72CA;
        margin-bottom: 14px;
    }

    &__middle-phone-wrapper {
        display: flex;
        align-items: center;
        margin-bottom: 7px;
    }

    &__middle-phone {
        font-family: 'Gilroy';
        font-style: normal;
        font-weight: 700;
        font-size: 16px;
        line-height: 20px;
        letter-spacing: -6.73319e-05px;
        color: #000000;
        padding: 8px 40px 8px 23px;
        background: #65E99F;
        border-radius: 18.5px;
    }

    &__phone-btn {
        border: none;
        padding: 9px 24px;
        font-family: 'Gilroy';
        font-style: normal;
        font-weight: 400;
        font-size: 15px;
        line-height: 18px;
        color: #000000;
        background: #41D481;
        border-radius: 18.5px;
        margin-left: -30px;
        cursor: pointer;
    }

    &__middle-author {
        display: flex;
        font-family: 'Gilroy';
        font-style: normal;
        font-weight: 400;
        font-size: 14px;
        line-height: 17px;
        letter-spacing: -3.17237e-05px;
        color: #000000;
        margin-top: 17px;
        margin-bottom: 24px;
    }

    &__author {
        font-family: 'Gilroy';
        font-style: normal;
        font-weight: 700;
        font-size: 14px;
        line-height: 17px;
        letter-spacing: -4.13787e-05px;
        text-decoration-line: underline;
        margin-left: 7px;
        color: #2AAE63;
        cursor: pointer;
    }

    &__middle-btns-wrapper {
        display: flex;
        align-items: center;
        gap: 11px;
        margin-bottom: 25px;
    }

    &__btn-middle {
        font-family: 'Gilroy';
        font-size: 14px;
        line-height: 16px;
        letter-spacing: -5.89155e-05px;
        color: #FFFFFF;
        padding: 15px 28px 14px 28px;
        background: #304FFF;
        border-radius: 23.5px;
        border: none;
        cursor: pointer;
    }

    &__middle-table {
        display: flex;
        flex-wrap: wrap;
        margin-bottom: 10px;

        .t {
            border-top: none;
        }

        .r {
            border-right: none;
        }

        .b {
            border-bottom: none;
        }

    }

    &__middle-tabel-item {
        width: 177px;
        padding: 13px 32px 22px 14px;
        border: 1px solid rgba(151, 151, 151, 0.191604);
    }

    &__middle-table-heading {
        font-family: 'Gilroy';
        font-size: 14px;
        line-height: 16px;
        letter-spacing: -0.00016833px;
        color: #444444;
    }

    &__middle-table-subtitle {
        font-family: 'Gilroy';
        font-style: normal;
        font-weight: 500;
        font-size: 18px;
        line-height: 22px;
        letter-spacing: -0.00016833px;
        color: #444444;

        span {
            font-family: 'Gilroy';
            font-style: normal;
            font-weight: 500;
            font-size: 18px;
            line-height: 22px;
            letter-spacing: -0.00016833px;
            text-decoration-line: underline;
            color: #4E72CA;
            cursor: pointer;
        }
    }

    &__middle-tabel-item-big {
        margin-bottom: 10px;
        border-top: 1px solid rgba(151, 151, 151, 0.191604);
        border-left: 1px solid rgba(151, 151, 151, 0.191604);
        padding-top: 14px;
        padding-left: 14px;
    }

    &__middle-feedback-text {
        font-family: 'Gilroy';
        font-style: normal;
        font-weight: 500;
        font-size: 17px;
        line-height: 25px;
        letter-spacing: -3.85217e-05px;
        color: #414D6A;
    }

    &__middle-feedback-btn-wrapper {
        display: flex;
        width: 361px;
        margin-top: 19px;
        justify-content: space-between;
        margin-bottom: 100px;
    }

    &__middle-feedback-watches {
        font-family: 'Gilroy';
        font-style: normal;
        font-weight: 500;
        font-size: 14px;
        line-height: 17px;
        letter-spacing: -5.89155e-05px;
        color: #000000;
    }

    &__middle-feedback-report {
        font-family: 'Gilroy';
        font-style: normal;
        font-weight: 500;
        font-size: 14px;
        line-height: 17px;
        letter-spacing: -5.89155e-05px;
        color: #D0021B;
        cursor: pointer;
    }

    &__middle-same-wrapper {
        width: 100%;
        display: flex;
        flex-direction: column;
    }

    &__middle-nav-link {
        font-family: 'Gilroy';
        font-style: normal;
        font-weight: 900;
        font-size: 24px;
        line-height: 30px;
        color: #4E72CA;
        text-decoration: none;
        position: relative;
        padding-bottom: 10px;
        margin-right: 90px;
    }

    &__middle-products-list {
        display: flex;
        justify-content: space-between;
    }

    &__line {
        display: inline-block;
        width: 100%;
        height: 2px;
        background-color: #D5D5D5;
        margin-bottom: 15px;
    }
}