.items {
    margin-top: 59px;
}

.items__list {
    display: flex;
    gap: 57px;
    position: relative;
    &::before {
        content: '';
        display: inline-block;
        position: absolute;
        left: -49px;
        top: 20px;
        background-image: url(./../../assets/icons/before__svg.svg);
        background-repeat: no-repeat;
        width: 46px;
        height: 46px;
    }
    &::after {
        content: '';
        display: inline-block;
        position: absolute;
        left: 1189px;
        top: 20px;
        background-image: url(./../../assets/icons/after__svg.svg);
        background-repeat: no-repeat;
        width: 46px;
        height: 46px;
    }
    
}

.items__item {
    align-items: center;
    display: flex;
    text-align: center;
    flex-direction: column;
    justify-content: space-between;
    transition: 0.4s ease;
    cursor: pointer;
    &:focus {
        transform: scale(0.95);
    }
    
    &:hover {
        transform: scale(1.04);
    }
    width: 81px;
    div {
        border-radius: 20px;
        padding: 11px 10px 12px 13px;
        &.one {
            background-color:  #B6CAFB;
        }
        
        &.two {
            background-color: #B4E2E1;
        }
        
        &.three {
            
            background-color: #FF9E83;
        }
        
        &.four {
            background-color: #F7E233;
        }
        
        &.five {
            background-color: #F6DAC2;
        }
        
        &.six {
            background-color: #B4E2E1;
        }
        
        &.seven {
            background-color: #B6CAFB;
        }
        
        &.eight {
            
            background-color: #F6DAC2;
        }
        
        &.nine {
            background-color: #FF9E83;
        }
    }
    
}

.items__text {
    font-weight: 700;
    font-size: 14px;
    line-height: 17px;
    color: #4A4A4A;
    width: 137px;
    margin-top: 6px;
}