footer {
    
}

.footer__wrapper {
    .footer__top {
        background-color: #282B30;
        padding-top: 29px;
        padding-bottom: 21px;
        .container {
            display: flex;
            align-items: center;
            .footer__top-img-wrapper {
                align-items: center;
                display: flex;
                gap: 4px;
            }
        }
    }
    .footer__top-title {
        font-weight: 700;
        font-size: 14px;
        line-height: 17px;
        color: #FFFFFF;
        margin-left: 14px;
    }
    .footer__top-text {
        font-weight: 500;
        font-size: 15px;
        line-height: 18px;
        color: #98A4B7;
        margin: 0;
        margin-left: 149px;
        margin-right: 95px;
    }
    .footer__all {
        background-color: #33383C;
        padding-top: 52px;
        height: 100%;
        padding-top: 52px;
        position: unset;
        left: 0;
        right: 0;
        bottom: 0;
        padding-bottom: 10px;
        width: 100%;
    }
    &p {
        font-family: 'Gilroy';
        font-weight: 500;
        font-size: 15px;
        line-height: 18px;
        color: #98A4B7;
        max-width: 303px;
        align-items: center;
        margin-top: 30px;
        margin-left: auto;
        margin-right: auto;
        margin-left: 261.5px;
        margin-bottom: 47px;
    }
}

.footer__list {
    display: flex;
    flex-wrap: wrap;
    gap: 29px;
    height: auto;
}

.footer__item {
    width: 246px;
    display: inline-block;
    margin-bottom: 30px;
    .one {
        ::marker {
            color: #FF5469;
        }
    }
    .two {
        ::marker {
            color:#F8E71C;
        }
    }
    .three {
        ::marker {
            color:#FF5469;
        }
    }
    .four {
        ::marker {
            color:#F5A623;
        }
    }
    .five {
        ::marker {
            color:#7ED321;
        }
    }
    .six {
        ::marker {
            color: #BD10E0;
        }
    }
    .seven {
        ::marker {
            color: #50E3C2;
        }
    }
    .eight {
        ::marker {
            color: #4A90E2;
        }
    }
}

summary {
    font-family: 'Gilroy';
    font-weight: 700;
    font-size: 14px;
    line-height: 17px;
    color: #FFFFFF;
}

.footer__item-i {
    margin-top: 12px;
    margin-left: 14px;
    font-family: 'Gilroy';
    font-weight: 500;
    font-size: 14px;
    line-height: 29px;
    color: #878F99;
    a {
        color: #878F99;    
    }
}

hr {
    border: none;
    border-top: 1px solid #4C565E;
}

.footer__bottom {
    text-align: center !important;
    text-align-last: center !important;
    summary {
        ::marker {
            position: absolute;
            text-align: end !important;
            text-align-last: end !important;
            background-position: 0 30px;
            right: 1px  !important;
        }
    }
}
.footer__b-list {
    display: flex;
    gap: 60px;
    .footer__b-item {
        color: #66717A;
        margin-top: 27px;
    }
    .active {
        color: #05F3B3;
    }
}

pre {
    font-family: 'Gilroy';
    font-size: 15px;
    line-height: 18px;
}

details {
    cursor: pointer;
}