header {
    background-color: #fff;
    user-select: none;
}

a {
    list-style-type: none;
    text-decoration: none;
}

.header__wrapper {
    display: flex;
    align-items: center;
    justify-content: space-between;
    
    a, pre{
        font-family: 'Gilroy';
        font-style: normal;
        font-weight: 700;
        font-size: 17px;
        line-height: 21px;
        color: #4E72CA;
    }
    
    pre {
        cursor: none;
    }
    
    .header__left-wrapper {
        display: flex;
        max-width: 250px;
        width: 236px;
        gap: 14px;
        margin-right: 50px;
        
        .header {
            font-size: 10px;
        }
        
        P {
            font-family: 'Gilroy';
            font-style: normal;
            font-weight: 700;
            font-size: 15px;
            line-height: 17px;
            color: #000000;
            margin-top: auto;
            margin-bottom: auto;
            max-width: 250px;
            width: 236px;
        }
        
    }
    
}

.header__wrapper-right {
    display: flex;
    align-items: center;
    text-align: center;
}

.header__right-wrapper {
    display: flex;
    justify-content: space-between;
    max-width: 450px;
    width: 37%;
}

.header__form {
    display: flex;
    align-items: center;
    gap: 15px;
    position: relative;
    margin-left: 40px;
    margin-right: 66px;
    border: none;
    margin-left: 30px;
    
    input {
        &:checked {
            border: 4px solid rgba(246, 218, 194, 0.831324);
        }
    }
    
}
.header__form-token {
    display: flex;
    align-items: center;
    gap: 15px;
    position: relative;
    margin-left: 100px;
    margin-right: 66px;
    border: none;
    
}

.header__lang {
    
    border: '3px solid black';
    width: '40px';
    height: '40px';
    margin: 10px;
    
    &:checked {
        contain: '';
        border: 4px solid rgba(246, 218, 194, 0.831324);
        background-color: #ff0;
        color: 4px rgba(246, 218, 194, 0.831324);
    }
}

input[type='radio'] {
    display: none;
}

label {
    padding-left: 10px;
    padding-right: 10px;
    z-index: 10;
    transition: all 1s ease;
}

label::before {
    content: '';
    display: flex;
    width: 20px;
    height: 20px;
    background-position: center;
    position: absolute;
    background-repeat: no-repeat;
    background-size: 110%;
    z-index: 1;
}

.header__lang:checked + label::before {
    background-image: url(./../../assets/img/line__active.svg);
}

.header__login {
    display: flex;
    justify-content: space-between;
}

.header__list {
    align-items: center;
    display: flex;
    gap: 43px;
    
    .header__item {
        display: flex;
        font-weight: 500;
        font-size: 15px;
        line-height: 18px;
        color: #000000;
        width: auto;
        .active {
            color: #05F3B3;
        }
    }
    
}

.header__login-wrapper {
    position: relative;
    width: auto;
}

.header__login-wrapper:hover .header__btn {
    display: flex;
}

.header__btn {
    display: none;
    font-family: "Gilroy";
    font-style: normal;
    font-weight: 500;
    font-size: 17px;
    line-height: 21px;
    text-align: center;
    color: #FFFFFF;
    background-color: #41D481;
    border-radius: 5px;
    border: none;
    outline: none;
    padding: 9px 23px;
    position: absolute;
    width: 111px;
    top: 0;
    left: 122px;
}

.header__img {
    border-radius: 50%;
}

.header__btn:hover {
    display: flex;
}